@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #1b2a49;
  color: #fff; }

.home__container .title {
  text-align: center;
  font-family: 'Permanent Marker', 'Sans Serif';
  font-size: 40px;
  color: #FFF; }

.home__container .game__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

.item__container {
  display: flex;
  flex-direction: row;
  max-height: 60px;
  margin: 5px 0;
  border: 0.5px solid #777;
  border-radius: 7px;
  background-color: #465881; }
  .item__container:hover {
    background-color: #00909e; }
  .item__container .item__image__container {
    max-height: 50px;
    width: 80px;
    max-width: 80px;
    margin-right: 20px;
    padding: 5px; }
    .item__container .item__image__container img {
      width: 100%;
      height: 100%; }
  .item__container .item__title {
    margin-right: 10px;
    font-size: 0.8rem; }

.videoList {
  height: 300px;
  max-height: 300px;
  overflow-y: auto; }

.playlist__item__container {
  position: relative;
  display: flex;
  flex-direction: row;
  max-height: 40px;
  margin: 0;
  border: 0.5px solid #777;
  background-color: #465881; }
  .playlist__item__container:hover {
    background-color: #00909e; }
  .playlist__item__container .playlist__item__image__container {
    padding: 2px;
    max-height: 35px;
    width: 80px;
    max-width: 80px;
    margin-right: 20px; }
    .playlist__item__container .playlist__item__image__container img {
      width: 100%;
      height: 100%; }
  .playlist__item__container .playlist__item__title {
    font-size: 0.8rem; }
  .playlist__item__container .playlist__item__buttons {
    position: absolute;
    right: 5px;
    display: flex;
    flex-direction: column; }
    .playlist__item__container .playlist__item__buttons i {
      margin: 0 0 3px 0; }
      .playlist__item__container .playlist__item__buttons i:hover {
        color: #1b2a49; }

.playList {
  max-height: 300px;
  max-width: 400px;
  overflow-y: auto;
  margin: 0 auto; }
  .playList .playlist__title {
    text-align: center;
    font-family: 'Permanent Marker', 'Sans Serif';
    font-size: 20px; }

.player__container {
  width: 400px;
  max-width: 400px;
  margin-top: -40px; }
  .player__container .player__title {
    text-align: center;
    font-family: 'Permanent Marker', 'Sans Serif';
    font-size: 20px; }

.player__container {
  max-width: 400px; }

.master__container {
  margin: 0 auto;
  text-align: center; }


